<template>
  <div style="background: rgba(0, 0, 0, 0.36)">
    <Card style="width:100%;background: rgba(0, 0, 0, 0.36);border: none;border-bottom: white 1px solid">
      <Icon type="ios-bulb"/>
      说明
      <span style="color: #ffabfa">菜单管理</span>
    </Card>
    <Tabs style="color:white;font-weight: bold">
      <TabPane label="菜单列表" icon="logo-apple">
        <Card :bordered="false"
              style="width: 100%;padding:0px;background: rgba(0, 0, 0, 0.36);margin-top: -15px;">
          <div style="margin:-5px 10px 10px 0px">
            <Button icon="md-add" type="default" size="small" shape="circle" style="float: right"
                    @click="saveOrUpdatebserviceInfoForm('add',{type:'1'})" ghost>增加
            </Button>
            <Input v-model="bserviceInfoGrid.keyword" placeholder="搜索关键字" style="width: 500px">
              <Button @click="searchBserviceList(null,true)" slot="append" icon="ios-search"></Button>
            </Input>
          </div>
          <Table ref="table"
                 style="background: rgba(0, 0, 0, 0.36);"
                 :height="clientHeight"
                 :columns="bserviceInfoGrid.columns"
                 :loading="bserviceInfoGrid.loading"
                 @on-sort-change="searchBserviceList"
                 :data="bserviceInfoGrid.data" showStripe>
            <template slot-scope="{ row }" slot="name">
              <Row>
                <Col span="4" v-if="row.type==2">&nbsp;</Col>
                <Col span="8" v-if="row.type==3">&nbsp;</Col>
                <Col :span="16">
                  <div style="color: rgba(13,30,13,0.58);border-radius: 20px"
                       :style="{color:function(){
                                        if(row.type==2){return '#2d8cf0'}
                                        else if(row.type==3){return 'green'}
                                        else{return 'yellow'}
                                    }()}"
                  >
                    <Icon size="30" :type=row.icon></Icon>&nbsp;&nbsp;
                    <strong>{{ row.name }}</strong>&nbsp;&nbsp;
                  </div>
                </Col>
              </Row>
            </template>
            <template slot-scope="{ row }" slot="state">
              <strong>{{$util.localDict.common.state[row.state]}}</strong>
            </template>
            <template slot-scope="{ row }" slot="type">
              <strong>{{$util.localDict.bserviceinfo.type[row.type]}}</strong>
            </template>
            <template slot-scope="{ row,index }" slot="action">
              <div style="text-align: right">
                <Icon size="25" style="color: lightblue" type=md-search @click="showBserviceView(index)"></Icon>
                <Icon size="25" style="color: green" type=ios-create
                      @click="saveOrUpdatebserviceInfoForm('update',{type:row.type,id:row.id})"></Icon>
                <Icon size="25" style="color: red" type=ios-trash @click="removeBservice(row.id)"></Icon>
              </div>
            </template>
          </Table>
          <div style="margin: 10px;">
            <div style="float: right;">
              <Page style="height: 40px" show-total show-elevator show-sizer
                    :total="bserviceInfoGrid.pageData.total"
                    :current="bserviceInfoGrid.pageData.current"
                    :page-size="bserviceInfoGrid.pageData.size"
                    @on-change="changePageNum"
                    @on-page-size-change="changePageSize"
              />
            </div>
          </div>
        </Card>
      </TabPane>
      <TabPane label="Windows" icon="logo-windows">
      </TabPane>

      <TabPane label="Linux" icon="logo-tux">标签三的内容</TabPane>

    </Tabs>


    <Drawer v-model="bserviceInfoForm.show"
            draggable
            :closable="false"
            :mask-closable="false"
            width=1000>
      <div class="ivu-drawer-header">
        <Icon size="20" type="md-close" class="drawer-close-icon" @click="bserviceInfoForm.show=false"/>
        <div class="ivu-drawer-header-inner">{{bserviceInfoForm.title}}</div>
        <div class="" style="position: absolute;right: 20px;top:10px;">
          <Button type="primary" @click="saveOrUpdateBservice('bserviceInfoForm')">提交</Button>
        </div>
      </div>
      <Card :bordered="false" :style="{height:(clientHeight+170)+'px'}">
        <Form ref="bserviceInfoForm" :model="bserviceInfoForm.data"
              :rules="bserviceInfoForm.validate" :label-width="80">
          <FormItem label='服务名' label-position="left" prop="code">
            <Input class="" v-model="bserviceInfoForm.data.serviceName"
                   placeholder=""/>
          </FormItem>

          <FormItem label='所属区域' label-position="left" prop="code">
            <Input class="" v-model="bserviceInfoForm.data.area"
                   placeholder=""/>
          </FormItem>

          <FormItem label='表单字段' label-position="left" prop="code">
            <Input class="" v-model="bserviceInfoForm.data.formItems"
                   placeholder=""/>
          </FormItem>

          <FormItem label='状态' label-position="left" prop="code">
            <Input class="" v-model="bserviceInfoForm.data.state"
                   placeholder=""/>
          </FormItem>

          <FormItem label='服务描述' label-position="left" prop="code">
            <Input class="" v-model="bserviceInfoForm.data.serviceDesc"
                   placeholder=""/>
          </FormItem>

        </Form>
      </Card>
    </Drawer>

    <IconSelect v-on:iconSelectListen="selectIcon" :clientHeight="clientHeight"
                :show="bserviceInfoForm.iconSelect"></IconSelect>

    <Drawer v-model="bserviceInfoForm.bserviceView.show"
            draggable
            :closable="false"
            :mask-closable="false"
            width=1000>
      <!--头部 start-->
      <div class="ivu-drawer-header">
        <Icon size="20" type="md-close" class="drawer-close-icon" @click="bserviceInfoForm.bserviceView.show=false"/>
        <div class="ivu-drawer-header-inner">详情</div>
      </div>
      <!--头部 end-->
      <Card :bordered="false" :style="{height:(clientHeight+170)+'px'}">
        <Row type="flex" v-for="(item ,index) in bserviceInfoGrid.columns"
             v-if="item.key"
             :key="index"
             style="height: 40px;margin-top: 5px;background: rgba(4,9,7,0.2);line-height: 35px;border-radius: 6px">
          <Col span="4">
            <div style="color: rgb(167,225,230);padding: 5px;text-align: center;font-weight: bold">
              {{item.title}}
            </div>
          </Col>
          <Col span="20">
            <div style="color: rgba(159,216,209,0.65);padding: 5px;">
              <div v-if="item.key=='icon'">
                <Icon size="30" :type="bserviceInfoForm.bserviceView.data[item.key||item.slot]"/>
              </div>
              <div v-else>
                            <span v-if="bserviceInfoForm.bserviceView.data[item.key||item.slot]" style="color: yellow">
                              {{(bserviceInfoForm.bserviceView.data[item.key||item.slot])}}
                            </span>
                <span v-else>
                                                                </span>
              </div>
            </div>
          </Col>
        </Row>
      </Card>
    </Drawer>
  </div>
</template>

<script>
  import IconSelect from "../../components/IconSelect";

  export default {
    components: {IconSelect},
    props: ["clientHeight"],
    data() {
      return {
        styles: {
          height: 'calc(100% - 55px)',
          overflow: 'auto',
          paddingBottom: '53px',
          position: 'static'
        },
        bserviceInfoGrid: {
          keyword:"",
          sortAble:true,
          loading: true,
          columns: [
            {
              type: 'selection',
              width: 60,
              align: 'center',
              fixed: 'left',
            },
            {
              title: '操作',
              slot: 'action',
              width: 150,
              fixed: 'left'
            },
            {
              title: '服务名',
              key: 'serviceName',
              sortable: true,
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '所属区域',
              key: 'area',
              sortable:function () {
                return this.bserviceInfoGrid.sortAble
              },
              //width: 200,
              //fixed: 'left'
            },
            // {
            //   title: '表单字段',
            //   key: 'formItems',
            //   //width: 200,
            //   //fixed: 'left'
            // },
            {
              title: '状态',
              key: 'state',
              sortable: true,
              slot: 'state',
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '服务描述',
              key: 'serviceDesc',
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '创建人',
              sortable: true,
              key: 'createUserIdText',
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '创建时间',
              key: 'createTime',
              sortable: true,
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '修改人',
              key: 'updateUserIdText',
              sortable: true,
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '修改时间',
              sortable: true,
              key: 'updateTime',
              //width: 200,
              //fixed: 'left'
            },
          ],
          data: [],
          pageData: {
            total: 100,
            current: 1,
            size: 10
          }
        },
        bserviceInfoForm: {
          title: "添加",
          iconSelect: false,
          showField: {
            id:false,
            serviceName:false,
            area:false,
            formItems:false,
            state:false,
            serviceDesc:false,
            createUserId:false,
            createTime:false,
            updateUserId:false,
            updateTime:false,
          },
          select:{
            pid:[]
          },
          clearData: {
            id:'',
            serviceName:'',
            area:'',
            formItems:'',
            state:'',
            serviceDesc:'',
            createUserId:'',
            createTime:'',
            updateUserId:'',
            updateTime:'',
          },
          data: {
            id:'',
            serviceName:'',
            area:'',
            formItems:'',
            state:'',
            serviceDesc:'',
            createUserId:'',
            createTime:'',
            updateUserId:'',
            updateTime:'',
          },
          validate: {
            serviceName:[
              {required: true, message: '请输入服务名', trigger: 'blur'},
            ],
            area:[
              {required: true, message: '请输入所属区域', trigger: 'blur'},
            ],
            formItems:[
              {required: true, message: '请输入表单字段', trigger: 'blur'},
            ],
            state:[
              {required: true, message: '请输入状态', trigger: 'blur'},
            ],
            serviceDesc:[
              {required: true, message: '请输入服务描述', trigger: 'blur'},
            ],
          },
          show: false,
          bserviceView:{
            data: {},
            show: false
          }
        }
      }
    },
    methods: {
      checkBserviceName() {

      },
      showBserviceView(index) {
        this.bserviceInfoForm.bserviceView.show = true;
        this.bserviceInfoForm.bserviceView.data = this.bserviceInfoGrid.data[index]
      },
      removeBservice(id) {
        const that = this;
        that.$Modal.confirm({
          title: "警告",
          content: "确定要删除吗？",
          onOk: function () {
            that.$util.req("/api/security/bservice/delBservice")
              .setJsonDataStr({ids: id})
              .setSuccessfn(function (result) {
                if (result.code === 1000) {
                  that.$Notice.success({
                    title: '操作成功',
                    desc: 'Bservice删除成功'
                  });
                  that.searchBserviceList()
                }else{
                  that.$Notice.error({
                    title: '操作失败',
                    desc: '${cfg.UpModuleName}删除失败'
                  });
                }
              }).go(that)
          }
        })
      },
      changeType(value, callBack) {
        callBack = callBack || function () {
        }
        if (value == "1") {
          this.bserviceInfoForm.showField.pid = false
          this.bserviceInfoForm.showField.viewPath = false
          this.bserviceInfoForm.showField.groupName = false
          this.bserviceInfoForm.data.pid = '0'
          callBack()
        } else if (value == "2" || value == "3") {
          if (value == "2") {
            this.bserviceInfoForm.showField.groupName = true
          }
          if (value == "3") {
            this.bserviceInfoForm.showField.groupName = false
          }
          this.bserviceInfoForm.showField.pid = true
          this.bserviceInfoForm.showField.viewPath = true
        }
      },
      selectIcon: function (fun) {
        fun(this)
      },
      saveOrUpdatebserviceInfoForm(actiontype, data) {
        if (actiontype === "add") {
          this.bserviceInfoForm.title = "添加"
          this.bserviceInfoForm.data = this.bserviceInfoForm.clearData
          this.changeType(data.type)
        } else if (actiontype === "update") {
          this.bserviceInfoForm.title = "修改"
          const that = this;
          that.$util.req("/api/security/bservice/searchBservice")
            .setJsonDataStr({'id': data.id})
            .setSuccessfn(function (result) {
              let bserviceInfo = result.data
              that.bserviceInfoForm.data = bserviceInfo
            })
            .go(that)
        }
        this.bserviceInfoForm.show = true
      },
      saveOrUpdateBservice(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            const that = this;
            that.$util.req("/api/security/bservice/saveOrUpdateBservice")
              .setJsonDataStr(that.bserviceInfoForm.data)
              .setSuccessfn(function (result) {
                if (result.code === 1000) {
                  that.$Notice.success({
                    title: '操作成功',
                    desc: 'bservice' + that.bserviceInfoForm.title + '成功'
                  });
                  that.searchBserviceList();
                  that.bserviceInfoForm.show = false;
                }})
              .setErrorfn(function (e) {})
              .go(this)
          } else {
            //this.$Message.error('Fail!')
          }
        })
      },
      searchBserviceList(orderObj,keywordSearch) {
        const that = this;
        that.bserviceInfoGrid.loading=true
        let orderMap;
        if(orderObj){
            orderObj.order=(orderObj.order=="normal"?"desc":orderObj.order)
            orderMap={}
            orderMap[orderObj.key]=orderObj.order;
        }
        if(keywordSearch){
          that.bserviceInfoGrid.sortAble=false
          that.bserviceInfoGrid.pageData.current=1
        }
        that.$util.req("/api/security/bservice/searchBserviceList")
          .setJsonDataStr({
            start:that.bserviceInfoGrid.pageData.current,
            size:that.bserviceInfoGrid.pageData.size,
            keyword:that.bserviceInfoGrid.keyword,
            orderMap:orderMap||{
              "create_time":"desc"
            }
          })
          .setSuccessfn(function (result) {
            that.bserviceInfoGrid.loading=false
            if (result.code === 1000) {
              that.bserviceInfoGrid.columns=that.bserviceInfoGrid.columns
              that.bserviceInfoGrid.data = result.data.records
              that.bserviceInfoGrid.pageData.current = result.data.current;
              that.bserviceInfoGrid.pageData.total = result.data.total;
            }
          })
          .setErrorfn(function (e) {})
          .go(that)
      },
      changePageNum(pageNum) {
        this.bserviceInfoGrid.pageData.current = pageNum;
        this.searchBserviceList()
      },
      changePageSize(pageSize) {
        this.bserviceInfoGrid.pageData.size = pageSize;
        this.searchBserviceList()
      }
    }
    ,
    mounted() {
      this.searchBserviceList();
    }
  }
</script>

<style>

</style>



